<template>
  <div class="filter-type">
    <p>{{filters.name}}</p>
    <div 
    class="img" 
    :style="{background: 'url('+ image +')'}" 
    :class="filters.name"
    @click="selectFilter"></div>
  </div>
</template>

<script>
import eventBus from "../event-bus.js" 
export default {
    props: {
        filters: Object,
        image: String
    },
    methods: {
      selectFilter(){
        eventBus.$emit('Filter',this.filters.name)
      }
    }
};
</script>

<style lang="scss" src="../styles/filter-type.scss"></style>
