<template>
    <div class="phone-body">
        <transition name="fade">
        <div class="feed" v-if="step === 1" v-dragscroll>
           <Vuegram-post v-for="(post,index) in posts" 
           :key="index" 
           :post="post"></Vuegram-post>
        </div>
        </transition>
        <transition name="fade">
        <div v-if="step === 2">
            <div class="selected-image"
            :style="{background: 'url('+ image +')'}"
            :class="selectFilter"></div>
            <div class="filter-container" v-dragscroll>
               <filter-type 
               v-for="(filter,index) in filters"
               :key="index"
               :filters="filter"
               :image="image"></filter-type>
            </div>
        </div>
        </transition>
        <transition name="fade">
        <div v-if="step === 3">
            <div class="selected-image"
            :style="{background: 'url('+ image +')'}"
            :class="selectFilter"></div>
            <div class="caption-container">
                <textarea 
                placeholder="Write a caption..."
                @input="inputText"
                :value="value"></textarea>
            </div>
        </div>
        </transition>
    </div>
</template>

<script>
import VuegramPost from "./VuegramPost.vue"
import filterType from "./filterType.vue"
export default {
    props: {
        posts: Array,
        step: Number,
        image: String,
        filters: Array,
        selectFilter: String,
        value: String
    },
    methods:{
        inputText(event){
            this.$emit('input',event.target.value)
        }
    },
    components: {
        VuegramPost: VuegramPost,
        filterType:filterType
    }
}
</script>

<style lang="scss" src="../styles/phone-body.scss"></style>
