<template>
    <div class="app-phone">
      <div class="phone-header">
          <transition name="fade">
            <a class="cancel-cta"
            v-if="step === 2 || step === 3"
            @click="goToHome">
            Cancel
            </a>
          </transition>
          <img src="../assets/vue_gram_logo_cp.png" alt=""> 
        <transition name="fade">
        <a class="next-cta"
        v-if="step === 2"
        @click="step++">
          Next
        </a>
        </transition>
        <transition name="fade">
        <a class="next-cta"
        v-if="step === 3"
        @click="share">
          Share
        </a>
        </transition>
      </div>
      <Phone-body :posts="posts"
       :step="step"
       :image="image"
       :filters="filters"
       :selectFilter="selectFilter"
       v-model="caption"></Phone-body>
      <div class="phone-footer">
        <div class="home-cta">
          <i class="fas fa-home fa-lg"
          @click="goToHome"></i>
        </div>
        <div class="upload-cta">
          <input type="file" id="file" name="file"
          @change="uploadImage"
          :disabled="step !== 1">
          <label for="file">
            <i class="far fa-plus-square fa-lg"></i>
          </label>
        </div>
      </div>
    </div>
</template>

<script>
import PhoneBody from "./PhoneBody.vue"
import posts from "../data/posts.js"
import filters from "../data/filters.js"
import eventBus from "../event-bus.js" 
export default {
  data(){
    return {
      posts,
      step: 1,
      image: "",
      filters,
      selectFilter: "",
      caption: ""
    }
  },
  created(){
    eventBus.$on('Filter',event =>{
      this.selectFilter = event;
    })
  },
  components: {
    PhoneBody: PhoneBody
  },
  methods: {
    uploadImage(e){
      const files = e.target.files;
      if(!files.length) return;
      const reader = new FileReader();
      reader.readAsDataURL(files[0]);
      reader.onload = e => {
        this.image = e.target.result;
        this.step = 2;
      }
    },
    goToHome(){
      this.image = "";
      this.selectFilter = "";
      this.step = 1;
      this.caption = "";
    },
    share(){
      const filter = {
        username: "daipi173",
        userImage: "https://s3-us-west-2.amazonaws.com/s.cdpn.io/1211695/me_3.jpg",
        postImage: this.image,
        likes: 0,
        hasBeenLiked: false,
        caption: this.caption,
        filter: this.selectFilter
      };
      this.posts.unshift(filter);
      this.goToHome();
    }
  } 
}
</script>
<style lang="scss" src="../styles/app.scss"></style>
<style>
.fade-enter-active, .fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>
